body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding-left: 24px;
	padding-right: 24px;
}

.App-link {
  color: #61dafb;
}

#home-link-container ul {
	list-style: none;
}
#home-link-container li {
	padding-top: 10px;
	padding-bottom: 10px;
}
#home-link-container a {
	color: #0088ce;
	text-decoration: none;
}

#home-link-container a:hover {
	text-decoration: underline;
}

.write-link-container {
	padding-left: 40px;
}

#main-text-sect {
	max-width: 420px;
	padding-left: 18px;
}

.animate-circle-stroke {
  stroke-dasharray: 220;
  stroke-dashoffset: 0;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes rotate {
  to {
    stroke-dashoffset: 220;
  }
}
@keyframes rotate {
  to {
    stroke-dashoffset: 220;
  }
}

.animate-circle-stroke-less-duration {
	-webkit-animation-duration: 4s !important;
	        animation-duration: 4s !important;
}

#main-text-sect {
	font-size: 24px;
}

.code-block {
	font-family: 'Courier New', Courier, monospace;
}

.node-small-circle {
	r: 10;
}
.vote-request-circle {
	fill: green;
}

.vote-ack-circle {
	fill: white;
	stroke: green;
	stroke-width: 3;
}

.leader-candidate-node,
.leader-node
 {
	 stroke: black;
	 stroke-width: 4;
}

.leader-candidate-node {
	stroke-dasharray: 4;
}

.visibility-hidden {
	visibility: hidden;
}
.visibility-visible {
	visibility: visible;
}

.pause-animation {
	-webkit-animation-play-state: paused; /* Safari 4.0 - 8.0 */
  animation-play-state: paused;
}

.animation-delay-2s {
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
}
.animation-delay-3s {
	-webkit-animation-delay: 3s;
	        animation-delay: 3s;
}
.animation-delay-4s {
	-webkit-animation-delay: 4s;
	        animation-delay: 4s;
}

.client-node {
	fill: #009900;
	r: 30;
}
.client-node-text,
.node-text {
	fill: white;
	text-anchor:middle;
	font-size: 18px;
  font-weight: bold;
}
.client-message {
	fill: black;
	r: 10;
}

.log-message {
	fill: red;
}
.log-message-ack {
	fill: white;
	stroke: red;
	stroke-width: 3;
}

.error-message {
	fill: black;
	content: 'X';
	color: red;
}

.set-text-uncommitted {
	fill: red;
}
.set-text-committed {
	fill: black;
}

.node-partition {
	/* r: 60; */
	fill: transparent;
	stroke-dasharray: 5;
	stroke: black;
	stroke-width: 2;
}

.ack-to-client {
	stroke: black;
	fill:transparent;
	stroke-width: 2;
}
.stale-data-text {
	fill: red;
}

#node-c-partition-wrap text {
	font-size: 14px;
}


.timer-face {
	stroke-dasharray: 189px;
	stroke-dashoffset: 0px;
	stroke: black;
}

.timer-countdown-animation {
	-webkit-animation: countdown 3s linear;
	        animation: countdown 3s linear;
}

@-webkit-keyframes countdown {
  from {
    stroke-dashoffset: 0px;
  }
  to {
    stroke-dashoffset: 189px;

  }
}

@keyframes countdown {
  from {
    stroke-dashoffset: 0px;
  }
  to {
    stroke-dashoffset: 189px;

  }
}

.control-btns {
  border-top: 1px solid;
	display: inline-block;
  margin-top: 10px;
	padding-top: 10px;
}

.yb-btn {
	width: 120px;
	height: 35px;
	border-radius: 4px;
	background: #202951;
	color: white;
	font-weight: bold;
	font-size: 16px;
	margin: 0 10px;
	cursor: pointer;
}
.yb-btn:not([disabled]):hover {
	background: #161C38;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.yb-btn:disabled {
	background: grey;
	cursor: not-allowed;
}

.yb-button-text {
  padding-left: 5px;
}

